@media only screen and (min-width : 320px) and (max-width : 720px) {
    .left-container {
        width: 100%;
        height: 100%;
    }
    
    .right-container {
        width: 100%;
        height: 100%;
    }
}

@media only screen and (min-width : 721px) {
    .section-container {
        width: 100%;
        height: 100%;
    }
    
    .flex-container {
        display: -webkit-flex;
        /* Safari */
        -webkit-flex-direction: row-reverse;
        /* Safari 6.1+ */
        display: flex;
        flex-direction: row;
    }
    
    .left-container {
        width: 50%;
        height: 100%;
    }
    
    .right-container {
        width: 50%;
        height: 100%;
    }
    
}

.skills-header {
    margin-left: 3%;
}

.skills-header h2 {
    color: #000;
    font-family: 'Roboto', sans-serif;
}

.skill-box {
    width: 100%;
    height: 15%;
    display: -webkit-flex;
    /* Safari */
    -webkit-flex-direction: row-reverse;
    /* Safari 6.1+ */
    display: flex;
    flex-direction: row;
}

.icon-size {
    width: 20%;
    height: 15%;
    margin: 2% 3% 2% 3%;
}
