@media only screen and (min-width : 320px) and (max-width : 767px) {
    .background-image{
        background-color: rgba(255, 255, 255, 0.35);
        width: 100%;
        height: 100%;
        position: relative;
    }

    .mask{
        background-image: url(./harish_self_background.png);
        background-size: 100% 55%;
        background-repeat: no-repeat;
        position: relative;
    }

    .page-div h1,
    h2,
    h3 {
        padding: 0px 0px 0px 0px;
        margin: 0px 0px 10px 0px;
        font-family: 'Questrial', sans-serif;
        color: grey;
    }

    .page-div h1 {
        font-size: 28px;
    }

    .page-div h2 {
        font-size: 24px;
    }

    .page-div h3 {
        font-size: 18px;
    }

    .left-intro {
        display: none;
    }

    .icons i {
        margin: 0% 7.5% 0% 7.5%;
    }

    .text-center {
        text-align: center;
    }

    .icons {
        margin-top: 7.5%;
        position: relative;
        font-size: 38px;
        text-align: center;
        color: grey;
    }

    .right-intro {
        position: absolute;
        width: 100%;
        top: 65%;
        bottom: 0%;
        max-height: 100%;
    }

}

@media only screen and (min-width : 768px) and (min-height : 540px){
    .background-image {
        width: 100%;
        height: 100%;
        background-image: url(./harish_background_new.png);
        background-size: cover;
        background-position: center;
        position: absolute;
    }
    .flex-layout {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
    }

    .page-div h1,
    h2,
    h3 {
        padding: 0px 0px 0px 0px;
        margin: 0px 0px 10px 0px;
        font-family: 'Questrial', sans-serif;
        color: whitesmoke;
    }

    .page-div h1 {
        font-size: 34px;
    }

    .page-div h2 {
        font-size: 30px;
    }

    .page-div h3 {
        font-size: 24px;
    }

    .left-intro {
        min-width: 57.5%;
        height: 100%;
    }

    .right-intro {
        position: relative;
        min-width: 42.5%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.55);
        z-index: +1;
    }

    .page-div .cover-text {
        position: relative;
        top: 45%;
        left: 20%;
        max-width: 75%;
    }

    .icons i {
        margin: 0% 7.5% 0% 7.5%;
    }

    .text-center {
        text-align: center;
    }

    #contact-box {
        position: relative;
        margin-top: 15%;
    }

    .icons {
        margin-top: 7.5%;
        position: relative;
        font-size: 38px;
        text-align: center;
        color: whitesmoke;
    }
}